.payment-list {
  @media (min-width: $m) {
    @include flex;
    gap: rem(18);
  }

  @media (min-width: $l) {
    gap: rem(15) rem(38);
  }

  &__item {
    padding-left: rem(30);
    padding-top: rem(10);
    padding-bottom: rem(10);
    background-size: 20px 20px;
    background-position: bottom 50% left;
    background-repeat: no-repeat;
    font-size: rem(13);
    line-height: lhPer(13, 16);
    color: #c3c3c3;

    &--qiwi {
      background-image: url("../../img/sprite.svg#qiwi");
    }

    &--yandex {
      background-image: url("../../img/sprite.svg#yandex");
    }

    &--web {
      background-image: url("../../img/sprite.svg#web");
    }
  }
}