.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 290px;
  margin: 0 auto;
}

.menu-button {
  position: absolute;
  top: 1.7rem;
  right: 50%;
  transform: translate(520%);
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: none;
  appearance: none;
}

.menu-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  mask-image: url("../img/sprite.svg#hamburger-menu-close");
  mask-repeat: no-repeat;
  mask-position: center;
  transition: 0.5s ease;
}

.menu-button--open-menu::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.text-accentuum {
  color: #3e9cdc;
}

.button {
  background-color: #ffffff;
  color: #2a2b30;
  text-decoration: none;
  padding: 0.56rem 1.12rem;
  border: 1px solid #6f6d7b;
  border-radius: 4px;
  box-sizing: border-box;
  min-width: 120px;
  text-align: center;
  text-transform: uppercase;
  font-size: 0.73rem;
  font-weight: 400;
  line-height: 123%;
}

.button:hover {
  background-color: #e5e5e5;
}

.button:active {
  background-color: #c6c4c4;
}

.button--dark {
  background-color: #3e9cdc;
  color: #ffffff;
  border-color: #ffffff;
}

.button--dark:hover {
  background-color: #2391da;
}

.button--dark:active {
  background-color: #1b73ae;
}

.html {
  font-size: 18px;
}

.body {
  background-color: #272733;
  color: #ffffff;
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
}

.header {
  background-color: rgba(16, 16, 29, 0.9);
  color: #ffffff;
  padding: 1.39rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  min-height: 80px;
}

.header__logo {
  width: 210px;
  object-fit: contain;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
  gap: 0.84rem 3.34rem;
  align-items: center;
  display: none;
}

.navigation__list--open {
  display: flex;
  flex-direction: column;
  position: absolute;
  align-items: center;
  top: 89px;
  left: 50%;
  transform: translate(-50%);
  width: 320px;
  background-color: rgba(16, 16, 29, 0.9);
  z-index: 1;
}

.navigation__list--open .navigation__item {
  width: 100%;
}

.navigation__list--open .navigation__link {
  padding: 0.7rem 2rem;
  text-align: center;
}

.navigation__link {
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.12rem;
  line-height: 123%;
  padding: 0.5rem 1.5rem;
  display: block;
}

.navigation__link:hover {
  color: #63b9f1;
}

.navigation__link--current {
  color: #43abf0;
}

.hero-block {
  color: #000000;
  background-image: url("../img/hero-block.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  padding-top: 7rem;
  padding-bottom: 2.78rem;
}

.hero-block__title {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
}

.hero-block__text {
  font-weight: 700;
  font-size: 1.12rem;
}

.hero-block__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.hero-block__list-item {
  position: relative;
  padding-left: 1.45rem;
}

.hero-block__list-item::before {
  content: "";
  position: absolute;
  top: 0;
  transform: translate(-0.89rem, 0.62rem);
  width: 0.28rem;
  height: 1.5px;
  background-color: #000000;
}

.hero-block__button-wrapper {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-top: 1.5rem;
  flex-wrap: wrap;
}

.main__wrapper {
  padding-bottom: 1.5rem;
  background-image: url("../img/back-main.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #272733;
  object-fit: cover;
  padding-top: 3.34rem;
}

.scheme__title {
  text-transform: uppercase;
  font-weight: 800;
  text-align: center;
  font-size: 2rem;
  line-height: 119%;
  margin-top: 0;
}

.scheme__hint {
  font-size: 0.89rem;
  font-weight: 400;
  line-height: 122%;
  color: #bbbbbb;
  text-align: center;
}

.form {
  font-size: 1rem;
  line-height: 122%;
  margin-top: 2.5rem;
}

.form__select {
  width: 100%;
  padding: 0.78rem 2.06rem 0.78rem 0.67rem;
  background-image: url("../img/sprite.svg#select-arrow");
  background-repeat: no-repeat;
  background-size: 18% 18%;
  background-position: bottom 50% right -5px;
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid #FFFFFF;
  border-radius: 3px;
  appearance: none;
  font-size: inherit;
  line-height: inherit;
  margin-bottom: 1.3rem;
  color: #272733;
}

.form__input {
  background-color: rgba(255, 255, 255, 0.85);
  outline: 1px solid #FFFFFF;
  outline-offset: -1px;
  border-radius: 3px;
  width: 100%;
  box-sizing: border-box;
  padding: 0.78rem 0.67rem;
  font-size: inherit;
  line-height: inherit;
  color: #272733;
  border: none;
}

.form__input::placeholder {
  color: #272733;
}

.form__input:not(:last-child) {
  margin-bottom: 1.3rem;
}

.form__input:focus {
  background-color: #ffffff;
  outline: solid 4px rgba(235, 235, 235, 0.85);
  outline-offset: -4px;
}

.form__range-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 0.5rem;
}

.form__range-label {
  width: 12.78rem;
  margin-bottom: 1rem;
}

.form__range-count {
  margin: 0;
  margin-top: 0.56rem;
}

.form__range {
  width: 100%;
}

.form__range {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  cursor: pointer;
  outline: none;
  border-radius: 15px;
  height: 0.34rem;
  background-color: rgba(255, 255, 255, 0.7);
  border: 1px solid #ffffff;
}

.form__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 24px;
  width: 24px;
  background-color: #42a9ed;
  border: 3px solid #286690;
  border-radius: 50%;
}

.form__range::-moz-range-thumb {
  height: 15px;
  width: 15px;
  background-color: #42a9ed;
  outline: 3px solid #286690;
  outline-offset: 0;
  border-radius: 50%;
  border: none;
}

.form__file {
  display: block;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
}

.form__file-text {
  font-size: 0.84rem;
  line-height: 120%;
  color: #272733;
  padding-left: 1.56rem;
  background-image: url("../img/sprite.svg#file-input");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom 50% right 77px;
}

.form__button {
  font-size: 0.84rem;
  line-height: 120%;
  width: 100%;
}

.list-step {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-top: 3rem;
}

.list-step__item {
  padding: 1.12rem 0.67rem 1.12rem 4.34rem;
  min-height: 4.2rem;
  box-sizing: border-box;
  position: relative;
}

.list-step__item::before {
  content: "";
  position: absolute;
  top: 0.56rem;
  left: 0.56rem;
  width: 2.62rem;
  height: 2.62rem;
  border-radius: 50%;
  background-color: #3e9cdc;
  border-color: #286690;
  border-width: 4px;
  border-style: solid;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
}

.list-step__item--find::before {
  background-image: url("../img/sprite.svg#find");
}

.list-step__item--percent::before {
  background-image: url("../img/sprite.svg#percent");
}

.list-step__item--file::before {
  background-image: url("../img/sprite.svg#file");
}

.list-step__item--email::before {
  background-image: url("../img/sprite.svg#email");
}

.list-step__item--bill::before {
  background-image: url("../img/sprite.svg#bill");
}

.list-step__item:not(:last-child) {
  margin-bottom: 0.84rem;
}

.list-step__text {
  margin: 0;
  overflow-wrap: break-word;
  hyphens: auto;
}

.footer {
  background-color: #3d4050;
}

.footer__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding: 1rem 0;
}

.footer__copyright {
  order: 1;
  margin: 0;
  text-align: center;
  font-size: 0.73rem;
  line-height: 123%;
}

.footer__copyright-upper {
  text-transform: uppercase;
}

.footer__payment-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding: 0.5rem 0;
}

.footer__contact-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding: 0.5rem 0;
}

.payment-list__item {
  padding-left: 1.67rem;
  padding-top: 0.56rem;
  padding-bottom: 0.56rem;
  background-size: 20px 20px;
  background-position: bottom 50% left;
  background-repeat: no-repeat;
  font-size: 0.73rem;
  line-height: 123%;
  color: #c3c3c3;
}

.payment-list__item--qiwi {
  background-image: url("../img/sprite.svg#qiwi");
}

.payment-list__item--yandex {
  background-image: url("../img/sprite.svg#yandex");
}

.payment-list__item--web {
  background-image: url("../img/sprite.svg#web");
}

.contact-list__link {
  display: block;
  padding-left: 1.67rem;
  padding-top: 0.56rem;
  padding-bottom: 0.56rem;
  background-size: 20px 20px;
  background-position: bottom 50% left;
  background-repeat: no-repeat;
  font-size: 0.73rem;
  line-height: 123%;
  color: #c3c3c3;
  text-decoration: none;
}

.contact-list__link--mail {
  background-image: url("../img/sprite.svg#mail");
}

.contact-list__link--vk {
  background-image: url("../img/sprite.svg#vk");
}

.select2-selection {
  height: 100%;
}

.select2-container .select2-selection--single {
  height: fit-content;
  padding: 0.62rem 2.06rem 0.62rem 0.67rem;
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid #FFFFFF;
}

.select2-container--default .select2-results__option--selected {
  color: #ffffff;
  background-color: transparent;
}

.select2-results__option {
  color: #ffffff;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: transparent;
  color: rgb(66, 169, 237);
}

.select2-search--dropdown {
  display: none;
}

.select2-results {
  background-color: rgba(61, 64, 80, 0.85);
}

.select2-results::-webkit-scrollbar-thumb {
  background-color: #3e9cdc;
  background: aqua;
}

.select2-results__options {
  padding: 0 0.56rem;
}

.select2-container--default .select2-results > .select2-results__options {
  background: rgba(39, 39, 51, 0.9);
  border: 1px solid #3D4050;
}

.select2-dropdown {
  border: 1px solid #3D4050;
}

.select2-container--default .select2-results > .select2-results__options {
  max-height: 10rem;
  scrollbar-color: #3e9cdc;
}

.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-thumb {
  background-color: #3e9cdc;
}

.select2-container--default.select2-container--open.select2-container--below .select2-selection--single,
.select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple {
  background-color: rgba(61, 64, 80, 0.85);
  border: 1px solid #3D4050;
  border-radius: 3px 3px 0px 0px;
}

.select2-container--open .select2-selection--single:focus .select2-selection__rendered {
  color: #ffffff;
}

.select2-results__options::-webkit-scrollbar-track {
  background-color: rgb(61, 64, 80);
  border-radius: 3px;
}

.select2-results__options::-webkit-scrollbar {
  width: 7px;
  background-color: rgba(39, 39, 51, 0.9);
}

.select2-results__options::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #3e9cdc;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  width: 20px;
  height: 20px;
  top: 50%;
  transform: translate(-0.55rem, -50%);
  mask-image: url("../img/sprite.svg#select-arrow");
  mask-repeat: no-repeat;
  mask-size: 65%;
  mask-position: bottom 50% right;
  background-color: #000000;
  transition: 0.2s ease-out;
}

.select2-container--default .select2-selection--single .select2-selection__arrow > b {
  display: none;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
  transform: translate(-0.55rem, -50%) scale(1, -1);
  background-color: #ffffff;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  display: none;
}

@media (min-width: 768px) {
  .container {
    width: 738px;
  }

  .menu-button {
    display: none;
  }

  .header__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .navigation__list {
    display: flex;
    flex-direction: row;
    gap: 0.56rem;
    margin-left: auto;
    max-width: 22.23rem;
  }

  .navigation__link {
    font-size: 0.73rem;
    padding: 0.28rem 0.45rem;
    font-weight: 500;
  }

  .hero-block__title {
    font-size: 1.89rem;
  }

  .hero-block__button-wrapper {
    margin-left: 2.23rem;
  }

  .main__wrapper {
    padding-top: 2.78rem;
    padding-bottom: 3rem;
  }

  .form__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem 1.39rem;
  }

  .form__select {
    margin: 0;
  }

  .form__input:not(:last-child) {
    margin: 0;
  }

  .form__range-wrapper {
    grid-row: 3/4;
    grid-column: 1/-1;
  }

  .form__range-label {
    width: auto;
  }

  .form__range-count {
    margin-top: 0.3rem;
  }

  .form__file {
    margin: 0;
    height: fit-content;
    margin: auto 0;
  }

  .form__button {
    grid-column: 1/-1;
    width: 20.56rem;
    margin: 0 auto;
  }

  .list-step {
    display: grid;
    grid-template-columns: repeat(5, 147px);
  }

  .list-step__item {
    padding: 3.89rem 0.84rem 0.56rem;
  }

  .list-step__item::before {
    top: 0;
    left: 50%;
    transform: translate(-50%, 0.56rem);
  }

  .list-step__item:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(2rem, 1.8rem);
    width: 4.17rem;
    height: 0.34rem;
    background-color: #c4c4c4;
    mask-image: url("../img/sprite.svg#step-mark");
    mask-repeat: no-repeat;
    mask-size: cover;
    mask-position: center;
  }

  .footer__item {
    position: relative;
  }

  .footer__item:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(1rem, -50%);
    width: 1px;
    height: 1rem;
    background-color: #c3c3c3;
  }

  .footer__wrapper {
    flex-direction: row;
    padding: 2rem 0;
  }

  .footer__copyright {
    order: 0;
    width: 10rem;
    text-align: left;
  }

  .footer__payment-list {
    padding: 0;
  }

  .footer__contact-list {
    padding: 0;
  }

  .payment-list {
    display: flex;
    flex-direction: row;
    gap: 1rem;
  }
}

@media (min-width: 1180px) {
  .container {
    width: 1172px;
  }

  .header {
    padding-top: 1.84rem;
    padding-bottom: 1.78rem;
  }

  .navigation__list {
    max-width: 38.89rem;
  }

  .hero-block {
    min-height: 850px;
    box-sizing: border-box;
    padding-top: 11.45rem;
  }

  .hero-block__title {
    max-width: 43rem;
    font-size: 2.67rem;
    line-height: 121%;
  }

  .hero-block__text {
    margin-top: 4.45rem;
    margin-bottom: 0.56rem;
    line-height: 120%;
  }

  .hero-block__list-item {
    font-size: 1.12rem;
    line-height: 120%;
  }

  .hero-block__button-wrapper {
    margin-left: 0;
    margin-top: 4.45rem;
  }

  .main__wrapper {
    padding-top: 6.67rem;
    padding-bottom: 8.34rem;
  }

  .scheme__title {
    margin-bottom: 1.67rem;
  }

  .form {
    margin-top: 4.45rem;
  }

  .form__wrapper {
    grid-template-columns: repeat(3, 370px);
    gap: 1.67rem;
  }

  .form__range-wrapper {
    grid-column: 1/3;
    grid-row: auto;
  }

  .form__range-label {
    margin-bottom: 1.1rem;
  }

  .form__range-count {
    margin: 0;
  }

  .form__file {
    padding: 0.8rem;
  }

  .form__button {
    margin-top: 0.56rem;
    padding: 0.8rem;
  }

  .list-step {
    margin-top: 4.45rem;
    grid-template-columns: repeat(5, 209px);
    gap: 1.67rem;
  }

  .list-step__item {
    padding: 6.67rem 0.56rem 0rem;
  }

  .list-step__item::before {
    width: 4.5rem;
    height: 4.5rem;
    background-size: 70%;
  }

  .list-step__item:not(:last-child)::after {
    width: 6.12rem;
    transform: translate(4rem, 3rem);
  }

  .list-step__text {
    font-size: 0.89rem;
    line-height: 119%;
    text-align: center;
  }

  .footer__item:not(:last-child)::after {
    transform: translate(2.5rem, -50%);
  }

  .footer__wrapper {
    align-items: center;
    padding: 1.67rem 0;
  }

  .footer__copyright {
    width: auto;
  }

  .payment-list {
    gap: 0.84rem 2.12rem;
  }

  .contact-list {
    display: flex;
    flex-direction: row;
    gap: 0.84rem 2.12rem;
  }
}

@media (max-width: 768px) {
  .footer__copyright {
    padding: 0 2rem;
  }

  .select2 {
    margin-bottom: 1.3rem;
  }
}

@media (min-width: 768px) and (max-width: 1180px) {
  .sentence--tablet {
    display: block;
  }
}